import React, { useCallback, useState, useMemo } from "react";
import { Post, Image, PostId } from "../types";
import { Button, Card, Dropdown, ListGroup } from "react-bootstrap";
import {
  ShareFill,
  Heart,
  ChatDots,
  ThreeDots,
  HeartFill,
} from "react-bootstrap-icons";
import "../styles/Post.scss";
import styled from "styled-components";

import PostImages from "./PostImages";
import { useSelector, useDispatch } from "react-redux";
import { RootState } from "../store/configureStore";
import { AppDispatch } from "../store/configureStore";
import { likePost, unlikePost } from "../reducers/post";

import Avatar from "react-avatar";
import CommentForm from "./CommentForm";
import PostCardContent from "./PostCardContent";
import FollowButton from "./FollowButton";

const CommentWrapper = styled.div`
  padding: 2px 15px;
  margin-bottom: 15px;
`;

const AvatarWrapper = styled(Avatar)`
  margin-right: 10px;
`;

const FolloWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  padding: 10px;
  background-color: #f0f2f5;
`;

const CommentNick = styled.div`
  font-weight: bold;

  & p {
    margin: auto;
    text-align: left;
    padding-left: 10px;
  }
`;

const CommentContent = styled.p`
  margin: auto;
  text-align: left;
  padding-left: 10px;
`;

const CommentLength = styled.div`
  margin-top: 10px;
  margin-bottom: 10px;
`;

const BottomLine = styled.p`
  border-bottom: 1px solid #e0e0e0;
`;

const ListGroupItem = styled(ListGroup.Item)`
  border: none;
  padding: 2px 0px;
`;

interface PostCardProps {
  post: Post;
  images?: Image[];
}

const PostCard: React.FC<PostCardProps> = ({ post }) => {
  const [commentFormOpened, setCommentFormOpened] = useState(false);
  // const [liked, setLiked] = useState(false);
  const dispatch = useDispatch<AppDispatch>();
  const id = useSelector((state: RootState) => state.user.me?.id);

  // const onToggleLike = useCallback(() => {
  //   setLiked((prev) => !prev);
  // }, []);

  // const onUnlike = useCallback(() => {
  //   const postData: PostId = {
  //     postId: post.id,
  //     userId: id,
  //   };
  //   dispatch(likePost(postData));
  // }, [dispatch, post.id, id]);

  // const onLike = useCallback(() => {
  //   const postData: PostId = {
  //     postId: post.id,
  //     userId: id,
  //   };
  //   dispatch(unlikePost(postData));
  // }, [dispatch, post.id, id]);

  const liked = useMemo(
    () => post.Likers?.find((v) => v.id === id),
    [post.Likers, id]
  ); // 앞의 값이 null, undefined일 경우 [] 임.

  const onToggleLike = useCallback(() => {
    if (!id) {
      return alert("로그인이 필요합니다.");
    }

    const postData: PostId = {
      postId: post.id,
      userId: id,
    };
    console.log(post.id, id);
    if (liked) {
      dispatch(unlikePost(postData));
    } else {
      dispatch(likePost(postData));
    }
  }, [id, liked, dispatch, post.id]);

  const onToggleComment = useCallback(() => {
    setCommentFormOpened((prev) => !prev);
  }, []);

  return (
    <div>
      <Card>
        <FolloWrapper>{id && <FollowButton />}</FolloWrapper>
        {post.Images && post.Images[0] && <PostImages images={post.Images} />}
        {/* <Card.Img variant="top" src="/images/art-1.jpg" /> */}
        <Card.Body>
          <Card.Title>
            <AvatarWrapper
              // name={post.User.nickname[0]}
              src="/images/sa.jpeg"
              size="40"
              round={true}
              textSizeRatio={2}
            />
            {post.User?.nickname}
          </Card.Title>
          <Card.Text>
            <PostCardContent postData={post.content || ""} />
          </Card.Text>
          <div className="card-button">
            <Button variant="white">
              <ShareFill />
            </Button>
            <Button variant="white" onClick={onToggleLike}>
              {liked ? (
                <HeartFill style={{ color: "red" }} />
              ) : (
                <Heart onClick={onToggleLike} />
              )}
            </Button>
            <Button variant="white" onClick={onToggleComment}>
              <ChatDots />
            </Button>
            <Dropdown>
              <span className="centered">
                <Dropdown.Toggle variant="white" id="dropdown-basic">
                  <ThreeDots />
                </Dropdown.Toggle>
              </span>
              <Dropdown.Menu>
                {post.User && id && post.User.id === id && (
                  <>
                    <Dropdown.Item href="#action1">수정</Dropdown.Item>
                    <Dropdown.Item href="#action2">삭제</Dropdown.Item>
                  </>
                )}

                {post.User && !id && (
                  <Dropdown.Item href="#action3">신고</Dropdown.Item>
                )}

                {/* {id && post.User.id === id ? (
                  <>
                    <Dropdown.Item href="#action1">수정</Dropdown.Item>
                    <Dropdown.Item href="#action2">삭제</Dropdown.Item>
                  </>
                ) : (
                  <Dropdown.Item href="#action3">신고</Dropdown.Item>
                )} */}
              </Dropdown.Menu>
            </Dropdown>
          </div>
        </Card.Body>
        {/* {commentFormOpened && post.Comments.length > 0 && ( */}
        {commentFormOpened && post.Comments && post.Comments.length >= 0 && (
          <CommentWrapper>
            <ListGroup>
              <CommentLength>
                {`${post.Comments.length}개의 댓글`}
              </CommentLength>
              <BottomLine />
              {post.Comments.map((item) => (
                <ListGroupItem key={post.id}>
                  <div className="d-flex align-items-center">
                    <AvatarWrapper
                      name={item.User?.nickname}
                      size="30"
                      round={true}
                    />
                    <CommentNick>{item.User?.nickname}</CommentNick>
                    <span>
                      <CommentContent>{item.content}</CommentContent>
                    </span>
                  </div>
                </ListGroupItem>
              ))}
            </ListGroup>
            <CommentForm post={post} />
          </CommentWrapper>
        )}
        {/* {commentFormOpened && (
          <CommentWrapper>
            <ListGroup>
              <CommentLength>
                {`${post.Comments.length}개의 댓글`}
              </CommentLength>
              <BottomLine />
              {post.Comments.map((item) => (
                <ListGroupItem key={post.id}>
                  <div className="d-flex align-items-center">
                    <AvatarWrapper
                      name={item.User?.nickname}
                      size="30"
                      round={true}
                    />
                    <CommentNick>{item.User?.nickname}</CommentNick>
                    <span>
                      <CommentContent>{item.content}</CommentContent>
                    </span>
                  </div>
                </ListGroupItem>
              ))}
            </ListGroup>
            <CommentForm post={post} />
          </CommentWrapper>
        )} */}
      </Card>
    </div>
  );
};

export default PostCard;
